.category-cards {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    max-width: 1000px;
    margin-top: 20px;
    padding: 15px 20px;
    background-color: rgba(17,23,53,.87);
    box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.1);
    .category-header {
        width: 100%;
        height: 2rem;
        font-size: 1rem;
        padding-left: 20px;
        color: #bff0ff;
        border-bottom: 1px solid #bff0ff;
        padding-top: 2px;
        font-weight: 800;
        margin-bottom: 5px;
    }
    .category-items {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 180px;
        height: 100%;
        border-radius: 3px;
        padding-left: 20px;
        .item-holder {
            width: 100%;
            .category-title {
                color: #a2e4f8;
                margin: 0;
                font-size: 0.8rem
            }
            .category-content {
                padding: 0;
                margin: 0;
                font-size: 1rem;
                color: #ffffff;
                font-weight: 600;
            }
        }
      }
}
